@import url('https://fonts.googleapis.com/css2?family=Syne:wght@400;500;600;700;800&display=swap');
.App {
  font-family: 'Syne' !important;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  place-items: center;
  width: 100%;
  min-height: 100vh;
  position: relative;
}
p
{
  font-family: 'Syne' !important;
  font-weight: 900;
  letter-spacing: 2px !important;
}
.App-logo {
  height: 720px;
  pointer-events: none;
  z-index: 5;
  position: relative;
  margin-bottom: -75px;
}

.header ul{
  display: flex;
  flex-direction: row;
}
.header
{
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  place-items: center;
  justify-content: space-around;
}
.header li {
  list-style: none;
  padding: 6px 12px;
 color: #000;
}
.header li a
{
  text-decoration: none;
  color: none;
  font-family: 'Syne' !important;
  font-weight: 600;
  color: #000;
}
span
{
  font-weight: 900;
}
.Logos
{
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  place-items: center;
  justify-content: space-around;
}
.logos-cnt
{
  
  width: 100%;
}
.logo-button
{
  height: 80px
}
button:disabled
{
  filter: saturate(0) opacity(0.5);
}
button
{
  border: none;
  filter: saturate(0) opacity(0.5);
  overflow: none;
  background: none;
  transition: all 0.1s ease-in-out;
}
button:hover
{
  filter: saturate(1) opacity(1);
  transform: scale(1.1);
  cursor: pointer;
}
button:hover:active
{
  filter: saturate(1) opacity(1);
  transform: scale(0.9);
  cursor: pointer;
}
.logo-vid
{
  position: absolute;
  z-index: 0;
  width: 420px;
  height: 420px;
  transform: 
  translateX(-70px)
  translateY(-70px);
  border-radius: 50px;
}
.horizontal-ticker
{
  width: 100% !important;
  margin: 0 auto !important;
} 
.url-container
{
  border: 5px solid #000;
  padding: 10px 16px;
  border-radius: 20px;
  background: white;
  box-shadow: 5px 5px 0px #000;

}
.tag
{
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 20px;
}
.logo-text {
  letter-spacing: -2px;
  font-size: 1.5rem;
  font-weight: 900;
}